  .weather--container {
    --tw-bg-opacity: 1;
    background-color: rgba(16,14,29,var(--tw-bg-opacity));
  }

  .weather--wrapper {
    width: 83.333333%;
    padding-top: 4rem;
    padding-bottom: 2.5rem;
    margin: auto;
  }

.temp--units--wrapper{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1.75rem;
    gap: 1rem
} 

.bg-light-gray,
.bg-dark-gray {
    cursor: pointer;
    border-radius: 9999px;
    width: 49.48;
    height: 52px;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: .75rem;
    padding-bottom: .75rem;
    font-size: 1.125rem;
    line-height: 1.75rem;
    font-weight: 700;
}

.bg-light-gray {
    --opacity: 1;
    color: rgba(30,33,58,var(--opacity));
    background-color: rgba(231,231,235,var(--opacity))
}

.bg-dark-gray {
    --opacity: 1;
    background-color: rgba(30,33,58,var(--opacity));
    color: rgba(231,231,235,var(--opacity));
}

.weather--subWrapper {
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    gap: 1.5rem;
}

.weather--wrapper--contents {
    --tw-bg-opacity: 1;
    background-color: rgba(30,33,58,var(--tw-bg-opacity));
    border-radius: .375rem;
    margin: auto;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.weather--date {
    --tw-text-opacity: 1;
    color: rgba(231,231,235,var(--tw-text-opacity));
    text-align: center;
    margin-top: 1rem;
}

.weather--images {
    width: 66.666667%;
    margin: auto;
} 

.weather--temp--wrapper {
    margin-top: 1rem;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.weather--unit {
    line-height: 1.75rem;
    font-size: 1.125rem;
    --tw-text-opacity: 1;
    color: rgba(160,159,177,var(--tw-text-opacity));
}

@media (min-width: 640px) {
    .weather--subWrapper{
      grid-template-columns: repeat(5,minmax(0,1fr));
    }
}